.featured {
    height: 90vh;
}

.featured--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal {
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 100px;
    padding-top: 70px;
}

.featured--name {
    font-size: 3.2rem;
    font-weight: bold;
}

.featured--info {
    display: flex;
    font-size: 1rem;
    font-weight: bold;
    margin-top: 15px;
}

.featured--points, 
.featured--year, 
.featured--seasons {
    margin-right: 15px;
}

.featured--points {
    color: #46d369;
}

.featured--description {
    margin-top: 15px;
    font-size: 1.1rem;
    color: #999;
    max-width: 40%;
}

.featured--buttons {
    margin-top: 15px;
}

.featured--watchbutton, 
.featured--mylistbutton {
    display: inline-block;
    font-size: 1.1rem;
    font-weight: bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
}

.featured--watchbutton:hover, 
.featured--mylistbutton:hover {
    opacity: 0.7;
} 

.featured--watchbutton {
    margin-top: 15px;
    background: #fff;
    color: #000;
}

.featured--mylistbutton {
    background: #333;
    color: #fff;
}

.featured--genres {
    margin-top: 15px;
    font-size: 1rem;
    color: #999
}


@media (max-width: 760px) {
    .featured--name {
        font-size: 2rem;
    }

    .featured--description {
        max-width: 100%;
        margin-right: 30px;
        font-size: 0.9rem;
    }

    .featured--watchbutton,
    .featured--mylistbutton {
        font-size: 1rem;
    }

    .featured--genres {
        font-size: 0.8rem;
    }
}
